
import { defineComponent, onMounted, computed, ref } from "vue";
import { ErrorMessage, Field, Form } from "vee-validate";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import { Actions } from "@/store/enums/StoreEnums";
import Swal from "sweetalert2/dist/sweetalert2.js";
import * as Yup from "yup";
import { useStore } from "vuex";

interface ProfileDetails {
  name: string;
  phone: string;
  market_name: string;
  market_address: string;
  market_tax_number: string;
  market_commercial_number: string;
  market_standard_number: string;
  market_site_url: string;
  market_email: string;
  market_phone: string;
  market_image: string;
  currency: string;
}

export default defineComponent({
  name: "account-settings",
  components: {
    ErrorMessage,
    Field,
    Form,
  },
  setup() {
    const submitButton1 = ref<HTMLElement | null>(null);
    const submitButton2 = ref<HTMLElement | null>(null);
    const submitButton3 = ref<HTMLElement | null>(null);
    const submitButton4 = ref<HTMLElement | null>(null);
    const submitButton5 = ref<HTMLElement | null>(null);
    const updateEmailButton = ref<HTMLElement | null>(null);
    const updatePasswordButton = ref<HTMLElement | null>(null);

    const emailFormDisplay = ref(false);
    const passwordFormDisplay = ref(false);
    const imgPreview = ref<string>("");
    const market_image = ref<File | null>(null);

    // const user_avatar = ref<File | null>(null);
    const store = useStore();
    const lang = computed(() => store.getters.getLanguage);
    const user = computed(() => store.getters.currentUser);

    const profileDetailsValidator = Yup.object().shape({
      name: Yup.string().required().label("name"),
      phone: Yup.string()
        .matches(/^[0-9]/, "Phone number must be 10 digits long")
        .min(10, "Phone number must be 10 digits long")
        .required("Phone number is required"),
    });

    const enterpriseDetailsValidator = Yup.object().shape({
      market_name: Yup.string().required().label("Enterprise"),
      market_address: Yup.string().required().label("Market Address"),
      market_tax_number: Yup.string()
        .min(15)
        .required()
        .label("market_tax_number"),
      market_commercial_number: Yup.string().label("Market Commercial Number"),
    });

    const changeEmail = Yup.object().shape({
      emailaddress: Yup.string().required().email().label("Email"),
      confirmemailpassword: Yup.string().required().label("Password"),
    });

    const changePassword = Yup.object().shape({
      currentpassword: Yup.string().required().label("Current password"),
      newpassword: Yup.string().min(4).required().label("Password"),
      confirmpassword: Yup.string()
        .min(4)
        .required()
        .oneOf([Yup.ref("newpassword"), null], "Passwords must match")
        .label("Password Confirmation"),
    });

    const profileDetails = ref<ProfileDetails>({
      name: "",
      phone: "",
      market_name: "",
      market_address: "",
      market_tax_number: "",
      market_commercial_number: "",
      market_standard_number: "",
      market_site_url: "",
      market_email: "",
      market_phone: "",
      market_image: "",
      currency: "",
    });

    const saveChanges1 = () => {
      if (submitButton1.value) {
        const userDetails = {
          name: profileDetails.value.name,
          phone: profileDetails.value.phone,
        };
        // Activate indicator
        submitButton1.value.setAttribute("data-kt-indicator", "on");
        store.dispatch(Actions.UPDATE_USER, userDetails).then((response) => {
          submitButton1.value?.removeAttribute("data-kt-indicator");
          Swal.fire({
            text: response.message,
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok, got it!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-primary",
            },
          });
        });
      }
    };

    const saveChanges2 = () => {
      if (submitButton2.value) {
        const formData = new FormData();

        const enterpriseDetails = {
          market_name: profileDetails.value.market_name,
          market_address: profileDetails.value.market_address,
          market_tax_number: profileDetails.value.market_tax_number,
          market_commercial_number:
            profileDetails.value.market_commercial_number,
          market_standard_number: profileDetails.value.market_standard_number,
          market_site_url: profileDetails.value.market_site_url,
          market_phone: profileDetails.value.market_phone,
        };

        if (market_image.value) {
          formData.append("market_image", market_image.value);
        }
        for (let key in enterpriseDetails) {
          formData.append(key, enterpriseDetails[key]);
        }

        // Activate indicator
        submitButton2.value.setAttribute("data-kt-indicator", "on");

        store.dispatch(Actions.UPDATE_USER, formData).then((response) => {
          submitButton2.value?.removeAttribute("data-kt-indicator");
          Swal.fire({
            text: response.message,
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok, got it!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-primary",
            },
          });
        });
      }
    };

    const saveChanges3 = () => {
      if (submitButton3.value) {
        // Activate indicator
        submitButton3.value.setAttribute("data-kt-indicator", "on");

        setTimeout(() => {
          submitButton3.value?.removeAttribute("data-kt-indicator");
        }, 2000);
      }
    };

    const saveChanges4 = () => {
      if (submitButton4.value) {
        // Activate indicator
        submitButton4.value.setAttribute("data-kt-indicator", "on");

        setTimeout(() => {
          submitButton4.value?.removeAttribute("data-kt-indicator");
        }, 2000);
      }
    };

    const deactivateAccount = () => {
      if (submitButton5.value) {
        // Activate indicator
        submitButton5.value.setAttribute("data-kt-indicator", "on");

        setTimeout(() => {
          submitButton5.value?.removeAttribute("data-kt-indicator");

          Swal.fire({
            text: "Email is successfully changed!",
            icon: "success",
            confirmButtonText: "Ok",
            buttonsStyling: false,
            customClass: {
              confirmButton: "btn btn-light-primary",
            },
          }).then(() => {
            emailFormDisplay.value = false;
          });
        }, 2000);
      }
    };

    const updateEmail = () => {
      console.log(updateEmailButton.value);

      if (updateEmailButton.value) {
        // Activate indicator
        updateEmailButton.value.setAttribute("data-kt-indicator", "on");

        setTimeout(() => {
          updateEmailButton.value?.removeAttribute("data-kt-indicator");

          emailFormDisplay.value = false;
        }, 2000);
      }
    };

    const updatePassword = () => {
      if (updatePasswordButton.value) {
        // Activate indicator
        updatePasswordButton.value.setAttribute("data-kt-indicator", "on");

        setTimeout(() => {
          updatePasswordButton.value?.removeAttribute("data-kt-indicator");

          Swal.fire({
            text: "Password is successfully changed!",
            icon: "success",
            confirmButtonText: "Ok",
            buttonsStyling: false,
            customClass: {
              confirmButton: "btn btn-light-primary",
            },
          }).then(() => {
            passwordFormDisplay.value = false;
          });
        }, 2000);
      }
    };

    const removeImage = () => {
      profileDetails.value.market_image = user.value.market_image;
      imgPreview.value = "";
    };

    const onFileChange = (e) => {
      const file = e.target.files[0];
      if (file) {
        imgPreview.value = URL.createObjectURL(file);
        market_image.value = file;
      } else {
        imgPreview.value = "";
      }
    };

    onMounted(() => {
      setCurrentPageBreadcrumbs("settings", ["account", "settings"]);
      setTimeout(() => {
        profileDetails.value = user.value;
        profileDetails.value.currency = store.state.currency;
      }, 1000);
    });

    return {
      submitButton1,
      submitButton2,
      submitButton3,
      submitButton4,
      submitButton5,
      saveChanges1,
      saveChanges2,
      saveChanges3,
      saveChanges4,
      deactivateAccount,
      profileDetails,
      emailFormDisplay,
      passwordFormDisplay,
      removeImage,
      onFileChange,
      imgPreview,
      profileDetailsValidator,
      enterpriseDetailsValidator,
      changeEmail,
      changePassword,
      updateEmailButton,
      updatePasswordButton,
      updateEmail,
      updatePassword,
      lang,
      user,
    };
  },
});
